import request from '@/utils/request'

export function shipperSearch(doc, type){
  return request({
    url: 'http://4plbox.test/api/shipperSearch/' + doc + '/' + type,
    method: 'get',
  })
}

export function save(data, type){
  return request({
    url: 'http://4plbox.test/api/shipperSave/' + type,
    method: 'post',
    data
  })
}

export function getCities(){
  return request({
    url: 'http://4plbox.test/api/ciudad/getSelectCity',
    method: 'get'
  })
}

export function getConsigneesByShipper(idShipper){
  return request({
    url: 'http://4plbox.test/api/getConsigneesByShipper/'+ idShipper,
    method: 'get'
  })
}

export function getConsigneesById(id){
  return request({
    url: 'http://4plbox.test/api/getConsigneesById/'+ id,
    method: 'get'
  })
}

export function getProductsPoint(){
  return request({
    url: 'http://4plbox.test/api/getProductsPoint',
    method: 'get'
  })
}



